import { wixClientServer } from "@/lib/wixClientServer";
import ProductPage from "../../Components/productList/productList";
import Filter from "@/Components/Filter/Filter";


const ProductListing = async ({searchParams}) => {
  const wixClient = await wixClientServer();

  const cat = await wixClient.collections.getCollectionBySlug(
    searchParams.cat || "all-products"
  );

  
  

  return (

    <section className="product_page">
      <div className="container">
        <div className="inner_">
          <Filter />
          <h3 className="title">{cat?.collection?.name} For you!</h3>
          <ProductPage 
          categoryId={cat.collection?._id || "00000000-000000-000000-000000000001"} 
          searchParams={searchParams}/>
          </div>
      </div>
    </section>
    
      
   
  )
}

export default ProductListing